<template>
    <div style="margin-right:3rem;" @click="checked = !checked">
        <input type="checkbox" 
            :id="collection" 
            :value="collection" 
            v-model="checked"
            class="hidden" />
        <label>
            <div v-bind:style="'background-image:url(' + file + '); background-position: 50% 0px; transform:scale(.97)'" 
                class="w-24 h-24 bg-cover mb-2 relative rounded-full shadow-md"></div>
        </label>
        <h5 class="text-sm text-center font-semibold uppercase mt-0">{{ collection }}</h5>
    </div>
</template>

<script>
export default {
    props: ['collection', 'file'],

    data() {
        return {
            checked: true,
        }
    },

    watch: {
        checked: function(value)
        {
            // value is a boolean
            if(!value) this.$emit('removed');
            if(value) this.$emit('added');
        }
    }
}
</script>




